






























































/* Placeholder Web Fonts */

/* Global Styling */
body {
	margin: 0;
	padding: 0;
	font-family: 'Archivo', 'Avenir Next', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
#app {
	position: relative;
	min-height: calc(100vh - 370px);
}
/* Transitions */
.page-fade-enter-active, .page-fade-leave-active {
	transition: .3s cubic-bezier(.33,.85,.85,.99);
}
.page-fade-enter {
	opacity: 0;
}
.page-fade-leave-to {
	opacity: 0;
}
.toolbar-fade-enter-active, .toolbar-fade-leave-active {
	transition: .17s cubic-bezier(.33,.85,.85,.99);
}
.toolbar-fade-enter {
	opacity: 0;
}
.toolbar-fade-leave-to {
	opacity: 0;
}
.search-fade-enter-active, .search-fade-leave-active {
	transition: .17s cubic-bezier(.33,.85,.85,.99);
}
.search-fade-enter {
	opacity: 0;
	transform: translateY(40px);
}
.search-fade-leave-to {
	opacity: 0;
	transform: translateY(40px);
}

/* Modal */
@keyframes popouts {
0% {
		transform: scale(0.9, 0.9);
}
100% {
		transform: scale(1, 1);
}
}
@keyframes bgcfade {
0% {
		background-color: rgba(28, 28, 28, 0);
}
100% {
		background-color: rgba(28,28,28,0.5);
}
}



