/* Shared SASS Definitions */
.window-title-bar[data-v-aee4aeae] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 24px;
  background: #414141;
  -webkit-app-region: drag;
  user-select: none;
  overflow: hidden;
}
.button-wrapper img[data-v-aee4aeae] {
  width: 32px;
  margin-top: -4px;
  margin-left: -4px;
  filter: invert(1);
  transition: 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.button-wrapper img[data-v-aee4aeae]:hover {
  opacity: 0.6;
  cursor: pointer;
}



